<template>
    <div id="app">
        <app-header></app-header>
        <router-view></router-view>
        <app-footer></app-footer>
    </div>
</template>

<script>
    import AppHeader from './components/app-header/AppHeader'
    import AppFooter from './components/app-footer/AppFooter'

    export default {
        components: {
            'app-header': AppHeader,
            'app-footer': AppFooter
        },
        name: 'app'
    }
</script>

<style lang="scss">
    @import "./assets/scss/normalize";
    @import "./assets/scss/font-awesome";
    @import "./assets/scss/tooltip";

    body {
        font-family: 'Lato', sans-serif;
        overflow-x: hidden;
    }
    .site-width {
        max-width: 1000px;
        margin: 0 auto;
    }
    .one-half {
        width: 50%;
    }
    .clearfix {
        &:before,
        &:after {
            content: '';
            clear: both;
            display: block;
        }
    }
    .section-title {
        font-size: 18px;
        font-weight: 700;
        color: #616161;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 8px;
    }
    .section-subtitle {
        font-size: 12px;
        font-weight: 700;
        color: #888;
        text-align: center;
        display: table;
        white-space: nowrap;
        margin-bottom: 20px;

        &:before, &:after {
            content: '';
            background-image: url("data:image/png;base64,");
            background-position: right 1em top 50%;
            top: 50%;
            width: 50%;
            background-repeat: no-repeat;
            position: relative;
            display: table-cell;
        }
        &:after {
            background-position: left 1em top 50%;
        }
    }
    .bullet {
        font-size: 10px;
        font-weight: 700;
        color: #C7A605;
        display: inline-block;
        padding: 3px;
        border: 1px solid #C7A605;
        border-radius: 2px;
        background-color: transparent;
        margin: 2px 5px 2px 0;
        text-decoration: none;
        transition: all 0.2s;

        &:hover {
            color: #000;
            background-color: #C7A605;
        }
    }
    .color-yellow {
        color: #C7A605;
    }
    .table {
        display: table;

        .table-row {
            display: table-row;
        }
        .table-cell {
            display: table-cell;
            padding: 3px 5px;

            &:first-child {
                padding-left: 0;
            }
            &:last-child {
                padding-right: 0;
            }
        }
    }
    .text-small {
        font-size: 12px;
    }
    .text-bold {
        font-weight: bold;
    }
</style>



